/* Profile Page Styling */

.userInfoContainer {
    background: -webkit-gradient(linear, left bottom, left top, from(#e6a8f8), to(#c173d0));
    background: -webkit-linear-gradient(bottom, #e6a8f8, #c173d0);
    background: -o-linear-gradient(bottom, #e6a8f8, #c173d0);
    background: linear-gradient(to top, #e6a8f8, #c173d0);
    -webkit-box-shadow: 0 2px 4px 0 rgba(38, 47, 42, 1.0), 0 6px 15px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 2px 4px 0 rgba(38, 47, 42, 1.0), 0 6px 15px 0 rgba(0, 0, 0, 0.19);
    border-radius: 4px;
    padding: 7%;
}



